<template>
    <div style="position: relative; z-index: 0">
        <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
            <FormItem prop="title" style="margin: 30px 0 0 30px; display: flex; justify-content: left;" >
                <i-input type="text" v-model="formInline.title" :placeholder="titleInputText"
                         style="width: 600px;" :disabled="ifAppend">
                    <Icon type="md-text" slot="prepend"></Icon>
                </i-input>
            </FormItem>
            <FormItem prop="tagAry" style="display: flex; justify-content: left; margin: 30px 0 0 30px" >
                <div style="display: flex; justify-content: left; align-content: center;">
                    <Tag type="dot" closable color="primary"
                         v-for="tagName in tags" :key="tagName" :name="tagName" @on-close="handleClose" >
                        {{ tagName }}
                    </Tag>
                    <i-input v-model="newTagText" style="width: 200px; margin: 1px" @on-enter="handleAdd" :disabled="ifAppend">
                        <Button slot="append" icon="ios-add" type="dashed" size="small" @click="handleAdd" :disabled="ifAppend">
                            {{ addTagText }}
                        </Button>
                    </i-input>
                </div>
            </FormItem>

            <div class="markdown" style="margin: 30px 30px 0 30px; position: relative; z-index: 0">
                <div class="container">
                    <mavon-editor v-model="content" ref="md" @imgAdd="$imgAdd" @imgDel="$imgDel"
                                  @change="change" :toolbars="toolbars" style="min-height: 500px"/>
                </div>
            </div>
            <FormItem>
                <Button type="primary" :loading="loading" shape="circle" icon="md-checkbox"
                        @click="handleSubmit('formInline')" style="margin-top: 20px">
                    
                </Button>
            </FormItem>
        </Form>

    </div>
</template>

<script>
    import { mavonEditor } from 'mavon-editor'
    import 'mavon-editor/dist/css/index.css'
    import httpSubmitPosting from "@/api/httpSubmitPosting";
    import httpAppendPosting from "@/api/httpAppendPosting";
    export default {
        name: "Edit",
        props: [],
        data () {
            return {
                string: '',
                minRows: {
                    minRows: 25
                },
                content:'', // markdown
                html:'',    // html
                configs: {},
                toolbars: {
                    bold: true, // 
                    italic: true, // 
                    header: true, // 
                    underline: true, // 
                    strikethrough: true, // 
                    mark: true, // 
                    superscript: true, // 
                    subscript: true, // 
                    quote: true, // 
                    ol: true, // 
                    ul: true, // 
                    link: true, // 
                    imagelink: true, // 
                    code: true, // code
                    table: true, // 
                    fullscreen: true, // 
                    readmodel: true, // 
                    htmlcode: true, // html
                    help: false, // 
                    /* 1.3.5 */
                    undo: true, // 
                    redo: true, // 
                    trash: false, // 
                    save: false, // eventssave
                    /* 1.4.2 */
                    navigation: true, // 
                    /* 2.1.8 */
                    alignleft: true, // 
                    aligncenter: true, // 
                    alignright: true, // 
                    /* 2.2.1 */
                    subfield: true, // 
                    preview: true, // 
                },
                loading: false,
                tags: [
/*                    {tagId: '0', tagName: 'tag0'},
                    {tagId: '1', tagName: 'tag1'},
                    {tagId: '2', tagName: 'tag2'},*/
                ],
                newTagText: '',
                formInline: {
                    title: '',
                    tagAry: []
                },
                // ruleInline: {
                //     title: this.titleRule,
                //     tagAry: this.tagRule
                // },
                userId: -1
            }
        },
        components: {
            mavonEditor,
        },
        computed: {
            ifAppend: function () {
                if(this.$route.query.postingId !== undefined)
                    return true
                return false
            },
            addTagText: function () {
                if(this.$route.query.postingId !== undefined)
                    return ''
                return ""
            },
            titleInputText: function () {
                if(this.$route.query.postingId !== undefined)
                    return ''
                return ""
            },
            ruleInline: function () {
                if(this.$route.query.postingId !== undefined)
                    return {}
                return {
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    tagAry: [
                        { required: true, type: 'array', min: 1, message: '1', trigger: 'blur, change' },
                        { type: 'array', max: 5, message: '5', trigger: 'blur, change' }
                    ]
                }
            }
        },
        methods: {
            // @imgAdd event
            $imgAdd(pos, $file){
                // ..
                var formdata = new FormData();
                formdata.append('image', $file);
                /*axios*/({
                    url: 'server url',
                    method: 'post',
                    data: formdata,
                    headers: { 'Content-Type': 'multipart/form-data' },
                }).then((url) => {
                    // .url![...](./0) -> ![...](url)
                    /**
                     * $vm mavonEditor
                     * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
                     * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
                     */
                    this.$vm.$img2Url(pos, url);
                })
            },
            $imgDel(pos) {
                window.console.log(pos)
            },
            // md
/*            $imgAdd(pos/!*, $file*!/){
                let formdata = new FormData();

                this.$upload.post('/', formdata).then(res => {
                    window.console.log(res.data);
                    this.$refs.md.$img2Url(pos, res.data);
                }).catch(err => {
                    window.console.log(err)
                })
            },*/
            // 
            change(value, render){
                // render  markdown [html]
                this.html = render;
            },
            // 
            submit(){
                this.loading = true
                if(this.$route.query.postingId !== undefined) {
                    httpAppendPosting.get(this.$route.query.postingId, this.content, data=>{
                        if(data == 200) {
                            this.loading = false
                            this.$Message.success('');
                            this.$router.push({path: '/index'})
                        }
                        else {
                            this.$Message.error('');
                        }
                    })
                }
                else {
                    httpSubmitPosting.get(this.formInline.title, this.tags, this.content, this.$root.userId, data=>{
                        if(data == 200) {
                            this.loading = false
                            this.$Message.success('');
                            this.$router.push({path: '/index'})
                        }
                        else {
                            this.$Message.error('');
                        }
                    })
                }
/*                window.console.log(this.content);
                window.console.log(this.html);
                this.$message.success('');*/
            },
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.submit();
/*                        this.$Message.success('Success!');*/
                    } else {
                        this.$Message.error('');
                    }
                })
            },
            handleAdd () {
                if(this.tags.length >= 5) {
                    this.$Message['warning']({
                        background: true,
                        content: '5'
                    });
                    return;
                }
                if(this.newTagText === undefined || this.newTagText === "") {
                    this.$Message['warning']({
                        background: true,
                        content: ''
                    });
                    return;
                }
                for(this.key in this.tags) {
                    if(this.tags[this.key] === this.newTagText) {
                        this.$Message['warning']({
                            background: true,
                            content: ''
                        });
                        return;
                    }
                }
                this.tags.push(this.newTagText)
                this.formInline.tagAry.push("");
                this.newTagText = ""
            },
            handleClose (event, name) {
                for(this.key in this.tags) {
                    if(this.tags[this.key] === name) {
                        this.tags.splice(this.key, 1);
                        this.formInline.tagAry.pop();
                        return;
                    }
                }
            }
        },
        mounted() {
            if(this.$root.hasLogin === false) {
                this.$nextTick(()=>{
                    this.$Message.error('');
                    this.$router.push({path: 'login'})
                })
            }
        }

    }
</script>

